.phone2 {
  height: 100vh;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;

  overflow: hidden;

  .header {
    display: flex;
    flex-direction: column;

    .get-access {
      align-self: flex-end;
      display: inline-flex;
      margin-top: 34px;
      margin-right: 39px;
      padding: 14px 24px;
      justify-content: center;
      align-items: center;
      gap: 10px;
      border-radius: 20px;
      background: #E1E1E1;
      background: color(display-p3 0.8813 0.8813 0.8813);
      color: #425A71;
      color: color(display-p3 0.2784 0.349 0.4353);
      text-align: center;
      font-family: 'Roboto', sans-serif;
      font-size: 16px;
      font-style: normal;
      font-weight: 700;
      line-height: normal;
      cursor: pointer;
    }

    h1 {
      color: black;
      text-align: center;
      font-family: 'Inter', sans-serif;
      font-size: 60px;
      font-style: normal;
      font-weight: 700;
      line-height: normal;
    }
  }

  .content {

    img {
      width: 16%;
      margin-left: 20%;
      justify-self: center;
      height: auto;
      object-fit: cover;
      z-index: 1000;
    }

    height: 80%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 40px;
    position: relative;

    .services {
      flex: 1;
      margin-left: 400px;
      position: absolute;
      z-index: 300;
      height: 80%;
      display: flex;
      gap: 40px;

      .service {
        width: 40%;

        display: flex;
        flex-direction: column;
        gap: 20px;

        img {
          object-fit: contain;
          width: 100%;
          height: 100%;
        }
      }
    }

    @keyframes movePhone {
      from {
        transform: translateX(0);
      }
      to {
        transform: translateX(-50vh);
      }
    }
    @keyframes moveService {
      from {
        display: flex;
        scale: 0;
        transform: translateX(-10vh);
      }
      to {
        scale: 100%;
        transform: translateX(20vh);
      }
    }
    @keyframes moveService2 {

      from {
        display: flex;
        scale: 0;
        transform: translateX(-100vh);
      }
      to {
        scale: 100%;
        transform: translateX(20vh);
      }
    }
    @keyframes fadeIn {
        from {
          opacity: 0;
        }
        to {
          opacity: 1;
        }
      }


  }


}