.country {
  height: 100vh;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  gap: 20px;
  overflow: hidden;
}
.country .header {
  display: flex;
  flex-direction: column;
}
.country .header .get-access {
  align-self: flex-end;
  display: inline-flex;
  margin-top: 34px;
  margin-right: 39px;
  padding: 14px 24px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-radius: 20px;
  background: #000;
  color: #FFF;
  text-align: center;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  cursor: pointer;
}
.country .header h1 {
  color: black;
  text-align: center;
  font-family: "Roboto", sans-serif;
  font-size: 60px;
  font-style: normal;
  font-weight: 900;
  line-height: normal;
}
.country .content {
  display: flex;
  height: 100%;
  width: 100%;
}
.country .content .image-container {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  opacity: 0;
}
.country .content .image-container:nth-child(1) {
  animation-delay: 0s;
}
.country .content .image-container:nth-child(2) {
  animation-delay: 1s;
}
.country .content .image-container:nth-child(3) {
  animation-delay: 2s;
}
.country .content .image-container:nth-child(4) {
  animation-delay: 3s;
}
.country .content .image-container:nth-child(5) {
  animation-delay: 4s;
}
.country .content .image-container:nth-child(6) {
  animation-delay: 5s;
}
@keyframes myAnim {
  0% {
    opacity: 0;
    transform: translateY(100vh);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes myAnimBack {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(100vh);
  }
}

@keyframes fadeTopCountries {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-100vw);
  }
}

.country .content .image-container img {
  width: 100%;
  object-fit: cover;
  height: 75%;
  z-index: 0;
  flex-shrink: 0;
}
.country .content .image-container .text {
  opacity: 0;
  color: #FFF;
  color: color(display-p3 1 1 1);
  text-align: center;
  font-family: Inter, sans-serif;
  font-size: 35px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  position: absolute;
  z-index: 10;
  height: 100%;
  top: 60%;
  left: 50%;
  transform: translateX(-50%);
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.country .content .image-container:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.35) 4.16%, rgba(0, 0, 0, 0.35) 20.93%), rgba(0, 0, 0, 0.35);
  box-shadow: -5px 4px 10px 0px rgba(0, 0, 0, 0.15);
  top: 0;
  z-index: 10;
}


